
import { defineComponent, reactive } from 'vue'
import { useRouter } from "vue-router"

import { getCode, signin, loginCode } from "@/api/index"
import { Toast } from 'vant'

export default defineComponent({
  name: 'login',
  components: {
  },
  setup(){
    const state = reactive({
      username: "",
      password: "",
      code: "",
      show: 1
    })

    const router = useRouter()

    // 密码注册
    const handlePassword = () => {
      state.username = ""
      state.password = ""
      state.code = ""
      state.show = 1
    }

    // 验证码注册
    const handleCode = () => {
      state.username = ""
      state.password = ""
      state.code = ""
      state.show = 2
    }

    // 发送二维码
    const handleSendCode = () => {
      getCode().then(res => {
        console.log("code---", res)
        if(res.code == 200){
          Toast({
            position: 'top',
            duration: 1000 * 60,
            message: "验证码: " + res.msg
          })
        }
      })
    }
    // 返回
    const handleGoBack = () => {
      router.go(-1)
    }

    // 注册登录
    const handleLogin = () => {
      // 注册登录
      if(state.show == 1){
        let data = {
          username: state.username,
          password: state.password
        }
        signin(data).then(res => {
          console.log("res===", res)
          if(res.code ===200){
            router.push("login")
            Toast(res.msg)
          }else{
            Toast(res.msg)
          }
        })
      }else{
        let data = {
          username: state.username,
          password: state.code
        }
        loginCode(data).then(res => {
          if(res.code === 200){
            Toast(res.msg)
            router.push("my")
          }else{
            Toast(res.msg)
          }
        })
      }
    }

    return {
      state,
      handlePassword,
      handleCode,
      handleGoBack,
      handleSendCode,
      handleLogin
    }
  }
})
